import React from "react"

export default function ComplyWiseFeatures() {
  const features = [
    {
      icon: "flaticon-view",
      title: "Consolidated View",
      description: "Single dashboard to manage GST notices across all clients."
    },
    {
      icon: "flaticon-stopwatch",
      title: "Real-time Tracking",
      description: "Monitors notice status and deadlines in real-time."
    },
    {
      icon: "flaticon-segmentation",
      title: "Intelligent Categorization",
      description: "Automatically sorts notices by type and urgency."
    },
    {
      icon: "flaticon-team",
      title: "Collaborative Workflows",
      description: "Facilitates team responses and approvals seamlessly."
    },
    {
      icon: "flaticon-idea",
      title: "AI-Powered Recommendations",
      description: "Get smarter compliance decisions with AI assistance."
    }
  ];

  return (
    <div className="container ptb-40">
      <h2 className="main-banner-image mt-4">ComplyWise Unique Features</h2>
      <div className="row justify-content pt-40">
        {features.map((feature, index) => (
          <div className="col-lg-4 col-md-6 col-sm-6 d-flex" key={index}>
            <div className="single-solutions-box single-solutions-box-hover p-4">
              <div className="icon">
                <i className={feature.icon}></i>
              </div>
              <h3>{feature.title}</h3>
              <p>{feature.description}</p>
            </div>
          </div>
        ))}
      </div>

      <div className="ptb-40">
        <blockquote>
                  <p className="text-lg text-gray-700 mb-2">
                    Discover how ComplyWise Digital can simplify GST notice management for your firm. Experience faster, smarter and more organised compliance workflows with our AI-powered platform.
                  </p>
                  <div className="option-item">
                    <a 
                    href="https://outlook.office.com/book/BookYourMeeting@appnetwise.com/?ismsaljsauthenabled=true" 
                    target="_blank" 
                    rel="noopener noreferrer" 
                    className="default-btn"
                  >
                    <i className="flaticon-right"></i> Schedule a Demo on ComplyWise Digital
                    <span></span>
                  </a>
                  </div>
                </blockquote>
      </div>
    </div>

  );
}
