import React from "react"
import complywise from "../../images/platform/complywise.svg"

export default function ComplyWiseHero() {
  return (
    <div className="saas-solution-area ptb-40">
      <div className="container">
        <div className="row align-items-center">
          <div className="col-lg-6 col-md-12">
            <div className="saas-solution-content text-center text-lg-start">
              <h1>ComplyWise Digital</h1>
              <h3>AI-powered GST Notice Management Platform</h3>
              <p>
                Streamline your GST notice management with our AI-powered platform designed specifically for CA firms and tax practitioners. ComplyWise Digital helps you manage, track, and respond to GST notices efficiently, reducing manual effort and minimizing compliance risks.
              </p>
              
              <div className="solution-btn">
                <a href="https://complywise.digital/" target="_blank" rel="noopener noreferrer" className="default-btn">
                      <i className="flaticon-right mr-4"></i>Learn More
                      <span></span>
                    </a>
                
              </div>
            </div>
          </div>

          <div className="col-lg-6 col-md-12">
            <div className="saas-solution-image">
              <img 
                src={complywise} 
                alt="ComplyWise Digital - GST Notice Management" 
                className="rounded"
              />
            </div>
          </div>
        </div>
      </div>

     
    </div>
  )
}
