import React from "react"
import Layout from "../components/_App/layout"
import Seo from "../components/_App/seo"
import Navbar from "../components/_App/Navbar"
import ComplyWiseHero from "../components/Product/ComplyWiseHero"
import Footer from "../components/_App/Footer"
import PageBanner from "../components/Common/PageBanner"
import ComplyWiseFeatures from '../components/Product/ComplyWiseFeatures';

const ComplyWisePage = () => {
  return (
     <Layout>
      <Navbar />

      <PageBanner
        pageTitle="ComplyWise Digital"
        homePageText="Home"
        homePageUrl="/"
        activePageText="Complywise"
      />
     <ComplyWiseHero />
     <ComplyWiseFeatures />
      <Footer />
    </Layout>
    
  )
}

export const Head = () => (
  <Seo 
    title="ComplyWise Digital - AI-powered GST Notice Management"
    description="ComplyWise Digital is an AI-powered GST notice management platform that helps CA firms and tax practitioners manage, track, and respond to GST notices seamlessly while reducing manual effort and risk."
  />
)

export default ComplyWisePage
